<template>
  <div>
    <Layout-box one="直播间商品">
      <el-card shadow="never" class="mb-1">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button style="float: right" type="primary" size="small" @click="onSearch">查询</el-button>
          <el-button style="float:right;margin-right: 15px" size="small" @click="onReset">重置</el-button>
        </div>
        <div class="mt-1">
          <el-form inline size="small" label-width="140px" :model="queryInfo">
            <el-form-item label="输入搜索：">
              <el-input v-model="queryInfo.name" style="width: 203px" placeholder="商品名称"></el-input>
            </el-form-item>
            <el-form-item label="上架状态：">
              <el-select v-model="queryInfo.state" placeholder="请选择">
                <el-option label="上架" value="1"></el-option>
                <el-option label="已下架" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
      <el-button size="small" @click="$router.push('/live/liveRoomAddGoods')">添加商品</el-button>
      <el-table size="small" :data="goodsList">
        <el-table-column prop="name" label="商品名" />
        <el-table-column label="状态">
          <template slot-scope="scope">
            <p v-if="scope.row.state === 0">下架中</p>
            <p v-else-if="scope.row.state === 1">上架中</p>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button v-if="scope.row.state === 0" size="small" @click="onState(scope, 1)">上架</el-button>
            <el-button v-else-if="scope.row.state === 1" size="small" @click="onState(scope, 0)">下架</el-button>
            <el-button size="small" @click="$router.push({ path: '/live/liveRoomEditGoods', query: { data: JSON.stringify(scope.row) }})">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </Layout-box>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox'

export default {
  name: 'live_room_goods',

  components: {
    LayoutBox
  },

  data () {
    return {
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      queryInfo: {
        name: '',
        state: ''
      },
      goodsList: [],
    }
  },

  async created () {
    await this.apiQueryAnchorProduct()
  },

  methods: {
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    // 点击上下架
    async onState (scope, state) {
      await this.apiUpdateAnchorProductState(scope, state)
    },
    // 点击查询
    async onSearch () {
      await this.apiQueryAnchorProduct()
    },
    // 点击重置
    async onReset () {
      this.queryInfo = {}
      await this.apiQueryAnchorProduct()
    },

    // -------------------------------------------------
    // 查询商品列表
    async apiQueryAnchorProduct () {
      const data = {
        ...this.queryInit,
        ...this.queryInfo
      }
      const res = await this.$http.post(this.$api.queryAnchorProduct, data)
      this.goodsList = res.data.data.list
    },
    // 上下架
    async apiUpdateAnchorProductState (scope, state) {
      const data = {
        produId: scope.row.id,
        state: state
      }
      const res = await this.$http.post(this.$api.updateAnchorProductState, data)
      if (res.data.success) {
        await this.apiQueryAnchorProduct()
        this.$message.success('操作成功')
      } else {
        this.$message.error(res.data.msg)
      }
    },
  }
}
</script>

<style lang="scss" scoped>

</style>
